import axios from 'axios';

const apiURLGetToken = 'http://localhost/_api/image/get/token';
const apiURLAdd      = 'http://localhost/_api/image/add';
const apiURLDelete   = 'http://localhost/_api/image/delete';

export default {
    add: function(data, callback) {
        this._query('add', data, callback);
    },
    delete: function(data, callback) {
        this._query('delete', data, callback);
    },
    getImageToken: function(data, callback) {
        this._query('getToken', data, callback);
    },
    _query: function(queryType, data, callback) {
        let formData = new FormData();
        let url = this._getUrl(queryType);
        let headers = {
            'Content-Type': 'multipart/form-data'
        };

        if (data['authRoken'] != undefined) {
            headers['X-AUTH-TOKEN'] = data['authRoken'];

            delete data['authRoken'];
        }

        console.log('data', data);

        for (let key in data) {
            formData.append(key, data[key]);
        }

        axios.post(url, formData, {
            headers: headers
        })
        .then(function (response) {
            callback(response.data);
        })
        .catch(function (e) {
            console.log('error');
            console.log(e);
        });
    },
    _getUrl: function(queryType) {
        if (queryType == 'add') {
            return apiURLAdd;
        }

        if (queryType == 'delete') {
            return apiURLDelete;
        }

        if (queryType == 'getToken') {
            return apiURLGetToken;
        }
    }
}