const initialState = {
    label: '',
    source: 'order',
    data: [],
    totalCount: 0,
    editFields: [
        {
            label: ' ',
            type: 'userSelect',
            name: 'user_id',
            value: 0
        },
        {
            label: '',
            type: 'order',
            name: 'products',
            value: 0
        }
    ],
    showedColumns: [
        {
            'label': 'Id',
            'name': 'id',
        },
        {
            'label': '',
            'name': 'user_id',
        },
        {
            'label': '',
            'name': 'amount',
        }
    ]
};

export default function (state = initialState, action) {
    let newState = Object.assign({}, state);

    if (action.type === 'ORDER_SET') {
        newState.data = action.data.results;
        newState.totalCount = action.data.totalCount;

        return newState;
    }

    if (action.type === 'ORDER_DELETE_BY_ID') {
        newState.data = newState.data.filter(obj => obj.id != action.id);

        console.log(newState.data);

        return newState;
    }

    if (action.type === 'ORDER_FIELDS_CHANGE') {
        newState.editFields = action.data;

        return newState;
    }

    return newState;
}
