import axios from 'axios';
const qs = require('query-string');

const apiURL = 'http://localhost/_api/';

export default {
    register(params, callback) {
        this.__query(apiURL + 'reg', params, callback);
    },
    auth(params, callback) {
        this.__query(apiURL + 'auth', params, callback);
    },
    logout(params, callback) {
        this.__query(apiURL + 'logout', params, callback);
    },
    getInfo(params, callback) {
        this.__query(apiURL + 'user-info', params, callback);
    },
    __query(url, params, callback) {
        let config = {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
        };

        if (params.token != undefined) {
            config.headers['X-AUTH-TOKEN'] = params.token;
        }

        delete params.token;

        axios.post(url, qs.stringify(params), config)
        .then((res) => {
            callback(res);
        })
        .catch((err) => {
            console.log(err);
        });
    }

}