import React from "react";
import Container from 'react-bootstrap/Container';
import { connect } from 'react-redux';
import Form from '../../../components/Form';
import {Link} from "react-router-dom"
import BaseEdit from "../BaseEdit";
import { Redirect } from 'react-router-dom';

class CategoryAdd extends BaseEdit {
    render() {
        if (!this.checkAccess(this.props.userRoles)) {
            return <Redirect to={'/'} />;
        }

        return (
            <div>
                <Container className="pt-2 pb-3 text-left">
                    <Link to={"/backoffice/" + this.getSource()}></Link>
                </Container>

                <Form
                    id={this.getId()}
                    token={this.props.token}
                    fields={this.props.editFields}
                    onChange={this.onChangeFields}
                    onSave={this.onSave} />
            </div>
        )
    }
}

export default connect(
    state =>  ({
        token: state.profile.token,
        userRoles: state.profile.userRoles,
        source: state.category.source,
        editFields: state.category.editFields
    }),
    dispatch => ({
        changeFields: (data) => {
            dispatch({ type: 'CATEGORY_FIELDS_CHANGE', data: data})
        }
    })
)(CategoryAdd);